#mainMenu{
    margin: 0;
    width: 99vw;
    height: 100vh;
    display: absolute;
    top: 0;
    border: 1px solid rgb(253, 253, 253);
    box-sizing: border-box;
    position: absolute;
	top: 50%;
	left: 50%;
    transform: translate(-50%, -50%);
    background-color: rgb(36, 36, 36);
    z-index: 10;
    color: bisque;
}
#mainMenu p{
    color: rgb(226, 226, 226);
    margin: 4px;
}

#mainMenu h3{
    margin-bottom: 4px;
}
#mainMenu h2{
    margin-bottom: 14px;
}


a{
    color: rgb(184, 255, 155);
}
